<template>
    <div class="credit-card-list">
      <div class="card-item" @click="toEditCard(item.id)" v-for="item in creditCardList" :key="item.num">
        <div class="card-type-con">
          <div class="card-logo">
            <span class="iconfont icon-yinhangqia"></span>
          </div>
          <div>
            <div class="card-name">
              {{item.name}}
            </div>
            <div class="card-type">
              {{item.type}}
            </div>
          </div>
        </div>
        <div class="card-num">{{item.num}}</div>
        <div class="card-user">
          {{item.userName}}
        </div>
      </div>
      <van-button  class="btn-login" type="default" plain @click="toAddCard">新增银行卡</van-button>
    </div>
</template>

<script type="text/ecmascript-6">
import { apiGet } from '../../common/js/api';
export default {
    name: "credit-card-list",
    
    data(){
      return{
        creditCardList:[]
      }
    },
    components: { },
    computed: {},
    watch:{},
    beforeCreate(){

    },
    created(){
      this.getCreditCardList();
    },
    beforeMount(){

    },
    mounted(){

    },
    methods:{
      //获取银行卡列表
      getCreditCardList(){
        apiGet("/bank/my-list",{
          accessToken:this.$cookie.get('accessToken')
        }).then(res=>{
          this.creditCardList = res.data.list
        })
      },
      //点击选择银行卡返回
      chooseItem(){
        setStore('chooseAdd',{
          addressId:chooseItemId,
          addressName:chooseItemName
        })
        this.$router.go(-1)
      },
      //添加银行卡
      toAddCard(){
        this.$router.push({
          path:'/addCreditCard',
        })
      },
      //编辑银行卡
      toEditCard(creditCardId){
        this.$router.push({
          path:'/editCreditCard',
          query:{
            creditCardId
          }
        })
      },
    }
}
</script>

<style scoped lang="scss">
  @import "./../../common/css/common.css";
  .credit-card-list{
    background: #E6E9ED;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding:.18rem .13rem;
  }
  .card-item{
    width:3.5rem;
    height:1.94rem;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    border-radius:7px;
    box-shadow:NaNpx NaNpx 3px 0px rgba(1,75,29,0.35);
    color: #fff;
    // display: flex;
    // flex-direction: column;
    padding:.3rem .2rem .2rem 0;
    .card-type-con{
      width: 100%;
      height: .48rem;
      padding-left:.17rem;
      display: flex;
      justify-content: space-between;
      .card-logo{
        width: .48rem;
        height: .48rem;
        border-radius: 50%;
        background: #7FAAF1;
        color: #fff;
        font-size: .3rem;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: .14rem;
      }
      &>div:nth-of-type(2){
        flex: 1;
        height: .48rem;;
      }
      .card-name{
        font-size: .19rem;
        margin-bottom: .05rem;
      }
    }
    .card-num{
      padding-left: .77rem;
      margin:.27rem 0 ;
    }
    .card-user{
      padding-left: .77rem;
    }
  }
  .btn-login{
    width: 3.06rem;
    height: .47rem;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    border-radius:0.04rem;
    font-size: 0.17rem;
    text-align: center;
    line-height: .47rem;
    outline: none;
    border: none;
    display: block;
    margin: 0 auto;
    color: white;
    margin-top:.5rem;
  }
</style>



